/* ******************************************************************************************************************** */
/* ON OFF SWITCH */
.onoffswitch {
    position: relative;
    width: 72px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

.onoffswitch-checkbox {
    position: absolute;
    opacity: 0;
    pointer-events: none;
}

.onoffswitch-label {
    display: block;
    overflow: hidden;
    cursor: pointer;
    /* border: 2px solid #999999; */
    border-radius: 15px;
    background-color: #FEC909;
}

.onoffswitch-inner {
    display: block;
    width: 200%;
    margin-left: -100%;
    transition: margin 0.3s ease-in 0s;
}

.onoffswitch-inner:before, .onoffswitch-inner:after {
    display: block;
    float: left;
    width: 50%;
    height: 30px;
    padding: 0;
    line-height: 30px;
    font-size: 14px;
    color: white;
    font-family: Trebuchet, Arial, sans-serif;
    font-weight: bold;
    box-sizing: border-box;
}

.onoffswitch-inner:before {
    content: "ON";
    padding-left: 7px;
    background-color: #FEC909;
    color: #FFFFFF;
    position: relative;
    left: -40px;
}

.onoffswitch-inner:after {
    content: "OFF";
    padding-right: 7px;
    background-color: #485E88;
    color: #FFFFFF;
    text-align: right;
}

.onoffswitch-switch {
    display: block;
    width: 21px;
    margin: 4.5px;
    background: #FFFFFF;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 38px;
    /* border: 2px solid #999999; */
    border-radius: 15px;
    transition: all 0.3s ease-in 0s;
}

.onoffswitch-checkbox:checked+.onoffswitch-label .onoffswitch-inner {
    margin-left: 0;
}

.onoffswitch-checkbox:checked+.onoffswitch-label .onoffswitch-switch {
    right: 0px;
}

/* switch two */

.onoffswitch-label2 {
    background-color: #099C3A;
}

.onoffswitch-inner2:before {
    background-color: #099C3A;
}

/* switch three */

.onoffswitch-label3 {
    background-color: #009AFE;
}

.onoffswitch-inner3:before {
    background-color: #009AFE;
}

